var __MOR_COMPONENT__ = require('@morjs/core').createComponent;

"use strict";
__MOR_COMPONENT__({
    externalClasses: ['wr-class'],
    properties: {
        goodsList: {
            type: Array,
            value: [],
        },
        id: {
            type: String,
            value: '',
            observer: (id) => {
                this.genIndependentID(id);
            },
        },
        thresholds: {
            type: Array,
            value: [],
        },
    },
    data: {
        independentID: '',
    },
    lifetimes: {
        ready() {
            this.init();
        },
    },
    methods: {
        onClickGoods(e) {
            const { index } = e.currentTarget.dataset;
            this.triggerEvent('click', Object.assign(Object.assign({}, e.detail), { index }));
        },
        onAddCart(e) {
            const { index } = e.currentTarget.dataset;
            this.triggerEvent('addcart', Object.assign(Object.assign({}, e.detail), { index }));
        },
        onClickGoodsThumb(e) {
            const { index } = e.currentTarget.dataset;
            this.triggerEvent('thumb', Object.assign(Object.assign({}, e.detail), { index }));
        },
        init() {
            this.genIndependentID(this.id || '');
        },
        genIndependentID(id) {
            if (id) {
                this.setData({ independentID: id });
            }
            else {
                this.setData({
                    independentID: `goods-list-${~~(Math.random() * Math.pow(10, 8))}`,
                });
            }
        },
    },
}, "w");
